a {
  text-decoration: none;
}
body{
  overflow-x: hidden;
  letter-spacing: 1px;
}
.footer{
  text-align: center;
  margin-top: 15px;
  color: #fff;
}
body{
  font-family: 'Karla',-apple-system,'BlinkMacSystemFont','Segoe UI','Roboto','Oxygen-Sans','Ubuntu','Cantarell','Helvetica Neue',sans-serif;
  height: 100%;
}
.social-icons{
  display: flex;
  padding: 0px;
  justify-content: space-around;
  list-style: none;
}
.SocialIconsStyle{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  height: 3rem;
  width: 3rem;
  background: #2196f3;
  border-radius: 50%;
  color: #fff;
  -webkit-transition: all .3ms ease-in;
  transition: all .3ms ease-in;
}
.container{
  background: #0e1621;
      height: -webkit-fill-available;
}

@media(min-width:1024px){
  .social-icons{
    align-items: center;
    margin: 20px auto;
    width: 50%;
  
  }
}